.arrow-line {
  position: relative;
  height: 1px;
  background: $color-line;
  &:before {
    opacity: 0;
    visibility: hidden;
    content: "";
    display: block;
    height: 100%;
    background: linear-gradient(to left, $color-gradient-darker 0%, $color-gradient 100%);
    transition: all 0.5s ease; }
  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: rem(6);
    height: rem(6);
    border: 1px solid;
    border-color: transparent transparent $color-line $color-line;
    transform-origin: center center;
    transform: translateY(-45%) rotate(45deg);
    transition: all 0.5s ease; }
  &--to-right {
    &:before {
      background: linear-gradient(to right, $color-gradient-darker 0%, $color-gradient 100%); }
    &:after {
      left: auto;
      right: 0;
      border-color: $color-line $color-line transparent transparent; } }
  .back-btn-group:hover &,
  .hub-link:hover & {
    &:before {
      visibility: visible;
      opacity: 1; }
    &:after {
      border-color: transparent transparent $color-gradient $color-gradient; }
    &.arrow-line--to-right {
      &:after {
        border-color: $color-gradient $color-gradient transparent transparent; } } } }
